<template>
  <div id="app">
    <section class="page-header">
      <h1 class="project-name">vue-daterange-picker</h1>
      <h2 class="project-tagline">vue daterange-picker component</h2>
      
        <a href="https://github.com/ilievZlatko/vue-daterange-picker" class="btn">View on GitHub</a>
    </section>

    <section class="main-content">
      <h1 id="vue-daterange-picker">vue-daterange-picker</h1>

      <p>A vue component for picking range of dates.</p>

      <p>start date: &nbsp;&nbsp; {{dates.startDate}}</p>
      <p>end date: &nbsp;&nbsp; {{dates.endDate}}</p>
      <div class="date-picker-container">
        <vue-daterange-picker 
          double 
          start-date="06/10/2017" 
          end-date="06/10/2018" 
          @get-dates="getDates"
        />
      </div>

      <h1 id="install">Install</h1>

      <div class="language-bash highlighter-rouge"><div class="highlight"><pre class="highlight"><code>npm install vue-daterange-picker <span class="nt">--save</span>
</code></pre></div></div>

      <h1 id="import-in-project">import in project:</h1>
      <div class="language-javascript highlighter-rouge"><div class="highlight"><pre class="highlight"><code> <span class="err">&nbsp;</span><span class="k">import</span> <span class="nx">VueDaterangePicker</span> <span class="k">from</span> <span class="s1">'vue-daterange-picker'</span><span class="p">;</span>
  
  <span class="nl">components</span><span class="p">:</span> <span class="p">{</span>
    <span class="nx">VueDaterangePicker</span>
  <span class="p">}</span>
</code></pre></div></div>

      <h1 id="dependencies">Dependencies:</h1>

      <h4 id="vue-clickaway--npm-install-vue-clickaway-save">vue-clickaway:  npm install vue-clickaway –save</h4>
      <h4 id="momentjs-npm-install-moment-save">momentJS: npm install moment –save</h4>

      <h1 id="props">Props:</h1>

      <p>Component expects properties:</p>
      <h4 id="start-date-string">start-date: String</h4>
      <h4 id="end-date-string">end-date: String</h4>
      <p>format ‘MM/DD/YY’.
      And then it will initiate callendar between these dates.</p>

      <h4 id="double-boolean">double: Boolean</h4>
      <p>display single callendar or double callendar.</p>

      <h4 id="format-string">format: String</h4>
      <p>changes the format of the inputs (default: ‘MM/DD/YYYY’)</p>

      <h4 id="title-format-string">title-format: String</h4>
      <p>changes the format of the calendar months (default: ‘MMMM Y’)</p>

      <h4 id="place-holders-string">place-holders: String</h4>
      <p>change the text in the placeholders.</p>
      <h4 id="example-place-holdersdates">example: place-holders=”dates”</h4>

      <h3 id="example">example:</h3>
      <p>When click away or hit escape key the component fires event ‘get-dates’ that contains an object
      with startDate and endDate keys holding values of start date and end date in utc format.</p>

      <pre><code class="language-HTML">  &lt;vue-daterange-picker 
            double 
            start-date="06/10/2017" 
            end-date="06/10/2018" 
            place-holders="mm/dd/yyyy"
            @get-dates="getDates"/&gt;
      </code></pre>


      <footer class="site-footer">
        
          <span class="site-footer-owner"><a href="https://github.com/ilievZlatko/vue-daterange-picker">vue-daterange-picker</a> is maintained by <a href="https://github.com/ilievZlatko">ilievZlatko</a>.</span>
        
        <span class="site-footer-credits">This page was generated by <a href="https://pages.github.com">GitHub Pages</a>.</span>
