    </section>
  </div>
</template>

<script>
import VueDaterangePicker from 'vue-daterange-picker';

export default {
	name: 'app',
	components: {
		VueDaterangePicker
	},
	data() {
		return {
			dates: {}
		};
	},
	methods: {
		getDates(dates) {
			this.dates = dates;
		}
	}
};
</script>

